import { memo, useCallback, useRef } from 'react';
import { Box } from 'rebass';
import useDropdown from '../../utils/useDropdown';
import useOnClickOutside from '../../utils/useClickOutside';
import { theadStyles } from './table.styles';
import List from '../list/container';
import ListItem from '../list/item';

export interface TheadProps {
  column: string;
  headerRender: (isOpen: boolean) => React.ReactNode;
  className?: string;
  actions: Array<{
    label: string;
    handler: (column: string) => void;
  }>;
}

const Thead = ({ column, className, headerRender, actions }: TheadProps) => {
  const containerRef = useRef(null);
  const [isOpen, handleToggle, handleClickOutside] = useDropdown();
  useOnClickOutside<HTMLDivElement>(handleClickOutside, [containerRef]);

  const dropdownStyles = {
    position: 'absolute',
    width: '100% !important',
    minWidth: 'max-content',
    left: '0',
    top: '37px',
    zIndex: '200',
  };

  const handleToggleList = useCallback(() => {
    handleToggle();
  }, [handleToggle]);

  return (
    <Box
      key={`thead-th-${column}`}
      as="th"
      sx={theadStyles}
      className={className}
      onClick={handleToggleList}
      ref={containerRef}
    >
      {headerRender(isOpen) as any}
      {isOpen && (
        <List sx={dropdownStyles}>
          {actions.map((action) => (
            <ListItem
              key={action.label}
              onClick={() => {
                action.handler(column);
              }}
            >
              {action.label}
            </ListItem>
          ))}
        </List>
      )}
    </Box>
  );
};

export default memo(Thead);
